.loader {
    display: none;
      animation: rotate 2s infinite;
      height: 70px;
      width: 70px;
      margin-left: 140px;
    }
  
  .loader.display{
    display: block;
    
  }
  
  .loader:before,
  .loader:after {
    border-radius: 50%;
    content: "";
    display: block;
    height: 20px;
    width: 20px;
  }
  
  .loader:before {
    animation: ball1 1s infinite;
    background-color: #ffd700;
    box-shadow: 30px 0 0 #0057b7;
    margin-bottom: 10px;
  }
  
  .loader:after {
    animation: ball2 1s infinite;
    background-color: #0057b7;
    box-shadow: 30px 0 0 #ffd700;
  }
  
  @keyframes rotate {
    0% { transform: rotate(0deg) scale(0.8) }
    50% { transform: rotate(360deg) scale(1.2) }
    100% { transform: rotate(720deg) scale(0.8) }
  }
  
  @keyframes ball1 {
    0% {
      box-shadow: 30px 0 0 #0057b7;
    }
    50% {
      box-shadow: 0 0 0 #0057b7;
      margin-bottom: 0;
      transform: translate(15px, 15px);
    }
    100% {
      box-shadow: 30px 0 0 #0057b7;
      margin-bottom: 10px;
    }
  }
  
  @keyframes ball2 {
    0% {
      box-shadow: 30px 0 0 #ffd700;
    }
    50% {
      box-shadow: 0 0 0 #ffd700;
      margin-top: -20px;
      transform: translate(15px, 15px);
    }
    100% {
      box-shadow: 30px 0 0 #ffd700;
      margin-top: 0;
    }
  }
  
